import { Component, Element, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() sizeSm?: string
  @Prop() sizeMd?: string
  @Prop() sizeLg?: string
  @Prop() sizeXl?: string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() offsetXl?: string
  @Prop() push?: string
  @Prop() pull?: string

  @Element() private element: HTMLElement

  componentDidLoad() {
    const ionCol = document.createElement('ion-col')
    ionCol.setAttribute('size', this.size ?? null)
    ionCol.setAttribute('size-sm', this.sizeSm ?? null)
    ionCol.setAttribute('size-md', this.sizeMd ?? null)
    ionCol.setAttribute('size-lg', this.sizeLg ?? null)
    ionCol.setAttribute('size-xl', this.sizeXl ?? null)
    ionCol.setAttribute('offset', this.offset ?? null)
    ionCol.setAttribute('offset-sm', this.offsetSm ?? null)
    ionCol.setAttribute('offset-md', this.offsetMd ?? null)
    ionCol.setAttribute('offset-lg', this.offsetLg ?? null)
    ionCol.setAttribute('offset-xl', this.offsetXl ?? null)
    ionCol.setAttribute('push', this.push ?? null)
    ionCol.setAttribute('pull', this.pull ?? null)

    const parent = this.element.parentElement
    if (parent) {
      parent.replaceChild(ionCol, this.element)
      ionCol.appendChild(this.element.childNodes[0])
    }
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    )
  }
}
